import React, { useState, useEffect } from 'react';
import { getFirestore, collection, query, where, orderBy, onSnapshot, Timestamp } from 'firebase/firestore';
import { StockItem, ActivityEvent, LogActionData, ActivityEventType } from './types';
import { getAvailableActions } from './ToolApp'; // For action buttons

// Helper function to format optional fields
const formatValue = (value: string | number | undefined | null, prefix = '', suffix = '') => {
  if (value === undefined || value === null || value === '') {
    return <span style={{ color: '#888' }}>N/A</span>; // Indicate if not available
  }
  return `${prefix}${value}${suffix}`;
};

// Helper function to format an activity event into a readable string
const formatActivityEvent = (event: ActivityEvent): string => {
  const time = new Date(event.timestamp).toLocaleString();
  switch (event.type) {
    case 'CREATED':
      return `${time}: Item Created`;
    case 'EDITED':
      const fields = event.details.changedFields?.join(', ') || 'details';
      return `${time}: Item Edited (${fields} changed)`;
    case 'STATUS_CHANGED':
      return `${time}: Status changed from ${event.details.previousStatus || '?'} to ${event.details.newStatus || '?'}`;
    case 'FLAG_TOGGLED':
      return `${time}: Item ${event.details.isFlagged ? 'Flagged' : 'Unflagged'}`;
    case 'ISSUE_REPORTED':
      return `${time}: Issue Reported: ${event.details.issueDescription || ''}`;
    case 'ISSUE_UPDATE_ADDED':
      return `${time}: Issue Update Added: ${event.details.note || ''}`;
    case 'ISSUE_RESOLVED':
      return `${time}: Issue Resolved (Outcome: ${event.details.resolutionOutcome || 'N/A'}${event.details.note ? ` - Note: ${event.details.note}` : ''}`;
    case 'NOTE_ADDED': // For general notes in future
      return `${time}: Note Added: ${event.details.note || ''}`;
    default:
      return `${time}: Unknown action`;
  }
};

// Helper function to map LogActionData from Firestore to the local ActivityEvent format
const mapLogToAction = (log: LogActionData): ActivityEvent | null => {
  // Handle Firestore Timestamp or ISO string
  const timestamp = log.timestamp instanceof Timestamp
    ? log.timestamp.toDate().toISOString()
    : (typeof log.timestamp === 'string' ? log.timestamp : new Date().toISOString());

  const userId = log.userId || 'system'; // Default if userId is missing

  let type: ActivityEventType | null = null;
  let details: ActivityEvent['details'] = {};

  // Map central log action strings to local ActivityEventType
  switch (log.action) {
    case 'item.create':
      type = 'CREATED';
      break;
    case 'item.update.fields':
      type = 'EDITED';
      // Attempt to get changed fields by comparing before/after, fallback to details or generic message
      const changed = log.before && log.after
        ? Object.keys(log.after).filter(key => log.before && log.after && JSON.stringify(log.before[key]) !== JSON.stringify(log.after[key]))
        : (log.details?.changedFields || ['details']);
      details = { changedFields: changed };
      break;
    case 'item.update.status.Delivered':
      type = 'STATUS_CHANGED';
      details = { previousStatus: log.before?.currentStatus, newStatus: 'Delivered' };
      break;
    case 'item.update.status.Archived':
      type = 'STATUS_CHANGED';
      details = { previousStatus: log.before?.currentStatus, newStatus: 'Archived' };
      break;
    // TODO: Add mappings for other specific status changes if implemented (e.g., 'Pending Delivery', 'Issue')
    case 'item.update.flag':
      type = 'FLAG_TOGGLED';
      details = { isFlagged: log.after?.isFlagged };
      break;
    case 'item.update.issue.report':
      type = 'ISSUE_REPORTED';
      details = { issueDescription: log.details?.description || log.details?.issueDescription }; // Allow for variations
      break;
    case 'item.update.issue.resolve':
      type = 'ISSUE_RESOLVED';
      details = { resolutionOutcome: log.details?.outcome, note: log.details?.note };
      break;
    case 'item.update.issue.update': // Action name used in handleSaveIssueUpdate via updateItemAndLog
      type = 'ISSUE_UPDATE_ADDED';
      details = { note: log.details?.note };
      break;
    case 'item.add_note': // Action name used in handleSaveNote via updateItemAndLog
      type = 'NOTE_ADDED';
      details = { note: log.details?.note };
      break;
    // Add more mappings if other log.action values exist
    default:
      console.warn(`Unhandled log action type for UI mapping: ${log.action}`);
      return null; // Don't display unmapped actions for now
  }

  if (!type) return null;

  return {
    timestamp,
    userId,
    // userName: // Optional: Could fetch/join user name later if needed
    type,
    details
  };
};

interface ItemDetailModalProps {
  item: StockItem;
  orgId: string;
  onClose: () => void;
  closeButtonRef?: React.RefObject<HTMLButtonElement | null>;
  onActionSelected: (itemId: string, action: string) => void;
}

const ItemDetailModal: React.FC<ItemDetailModalProps> = ({ item, orgId, onClose, closeButtonRef, onActionSelected }) => {
  // State for fetched logs
  const [activityEvents, setActivityEvents] = useState<ActivityEvent[]>([]);
  const [loadingLogs, setLoadingLogs] = useState<boolean>(true);
  const [logError, setLogError] = useState<string | null>(null);

  // Actions toolbar
  const availableActions = getAvailableActions(item.currentStatus, item.isFlagged);
  const actionContainerStyle: React.CSSProperties = { display: 'flex', justifyContent: 'flex-end', gap: '10px', marginBottom: '15px' };
  const actionButtonStyle: React.CSSProperties = { padding: '6px 12px', border: '1px solid #007bff', backgroundColor: '#007bff', color: 'white', borderRadius: '4px', cursor: 'pointer' };

  const modalStyle: React.CSSProperties = {
    backgroundColor: 'white',
    padding: '20px 30px',
    border: '1px solid #ccc',
    borderRadius: '8px',
    maxWidth: '600px',
    maxHeight: '80vh', // Prevent modal from being too tall
    overflowY: 'auto', // Allow scrolling if content overflows
    boxShadow: '0 4px 8px rgba(0,0,0,0.1)',
  };

  const overlayStyle: React.CSSProperties = {
    position: 'fixed',
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: 'rgba(0,0,0,0.5)',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    zIndex: 30, // Ensure it's above the AddItemForm if both were open
  };

  const detailRowStyle: React.CSSProperties = {
    marginBottom: '10px',
    paddingBottom: '10px',
    borderBottom: '1px solid #eee',
  };

  const labelStyle: React.CSSProperties = {
    fontWeight: 'bold',
    display: 'inline-block',
    minWidth: '150px',
    marginRight: '10px',
  };

  const historySectionStyle: React.CSSProperties = {
    marginTop: '20px',
    paddingTop: '15px',
    borderTop: '1px solid #ccc',
  };

  const historyItemStyle: React.CSSProperties = {
    marginBottom: '8px',
    paddingBottom: '8px',
    borderBottom: '1px dashed #eee',
    fontSize: '0.9em',
  };

  // useEffect to fetch logs from Firestore
  useEffect(() => {
    if (!item?.id || !orgId) { // Ensure we have item ID and orgId prop
        setLoadingLogs(false);
        setLogError("Missing item or organization ID for fetching logs.");
        return;
    }

    setActivityEvents([]); // Clear previous logs
    setLoadingLogs(true);
    setLogError(null);
    const db = getFirestore();
    const entityPath = `stockItems/${item.id}`;
    // Query the 'logs' collection using the orgId prop
    const logsRef = collection(db, `orgs/${orgId}/logs`);
    const q = query(logsRef, where('entityPath', '==', entityPath), orderBy('timestamp', 'desc'));

    const unsubscribe = onSnapshot(q, (snapshot) => {
      // Map the document data directly to LogActionData, assuming data matches the interface
      const fetchedLogs = snapshot.docs.map(doc => ({ ...doc.data() } as LogActionData));

      // Map LogActionData to ActivityEvent for display
      const mappedEvents = fetchedLogs
        .map(mapLogToAction)
        .filter((event): event is ActivityEvent => event !== null); // Filter out nulls and type guard

      setActivityEvents(mappedEvents);
      setLoadingLogs(false);
    }, (error) => {
      console.error("Error fetching logs: ", error);
      setLogError(`Failed to fetch activity log: ${error.message}`);
      setLoadingLogs(false);
    });

    // Cleanup listener on unmount or when item changes
    return () => unsubscribe();
  }, [item?.id, orgId]); // Re-run if item ID or orgId prop changes

  return (
    <div style={overlayStyle} onClick={onClose}> {/* Close on overlay click */}
      <div style={modalStyle} onClick={(e) => e.stopPropagation()}> {/* Prevent closing when clicking inside modal */}
        <h3>Item Details: {item.productName} - {item.deliveryName}</h3>
        <div style={actionContainerStyle}>
          {availableActions.map(a => (
            <button key={a} style={actionButtonStyle} onClick={() => onActionSelected(item.id, a)}>
              {a}
            </button>
          ))}
        </div>
        <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '12px', marginBottom: '20px' }}>
          <div><strong>ID:</strong> {item.id}</div>
          <div><strong>Status:</strong> {item.currentStatus}</div>
          <div><strong>Flagged:</strong> {item.isFlagged ? 'Yes' : 'No'}</div>
          <div><strong>Product:</strong> {item.productName}</div>
          <div><strong>Delivery:</strong> {item.deliveryName}</div>
          <div><strong>Quantity:</strong> {item.quantity}</div>
          <div><strong>Price/Item:</strong> {formatValue(item.pricePerItem, '')}</div>
          <div><strong>Order Date:</strong> {item.orderDate}</div>
          <div><strong>Purchase Status:</strong> {formatValue(item.purchaseStatus)}</div>
          <div><strong>Order #:</strong> {formatValue(item.orderNumber)}</div>
          <div><strong>Seller/Source:</strong> {formatValue(item.seller)}</div>
          <div><strong>Destination:</strong> {formatValue(item.destination)}</div>
          <div><strong>ASIN/SKU:</strong> {formatValue(item.asinSku)}</div>
          <div><strong>Acquisition Notes:</strong> {formatValue(item.acquisitionNotes)}</div>
          <div><strong>Date Delivered:</strong> {formatValue(item.dateDelivered ? new Date(item.dateDelivered).toLocaleString() : undefined)}</div>
          <div><strong>Issue Description:</strong> {formatValue(item.issueDescription)}</div>
          <div><strong>Processor Notes:</strong> {formatValue(item.processorNotes)}</div>
        </div>
        <details style={{ marginTop: '20px' }}>
          <summary style={{ cursor: 'pointer', fontWeight: 'bold' }}>History / Activity Log</summary>
          {loadingLogs && <div>Loading activity...</div>}
          {logError && <div style={{ color: 'red' }}>{logError}</div>}
          {!loadingLogs && !logError && activityEvents.length === 0 && <div>No activity recorded.</div>}
          <ul style={{ paddingLeft: '20px', marginTop: '10px' }}>
            {activityEvents.map((event, idx) => (
              <li key={idx} style={historyItemStyle}>{formatActivityEvent(event)}</li>
            ))}
          </ul>
        </details>

        <div style={{ marginTop: '20px', textAlign: 'right' }}>
          <button
            ref={closeButtonRef}
            onClick={onClose}
            style={{ padding: '8px 15px', fontWeight: 'bold' }}
          >
            Close
          </button>
        </div>
      </div>
    </div>
  );
};

export default ItemDetailModal;